*{
    padding: 0px;
    margin: 0px;
}

body{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.appclass{
  width: 100%;
  background-color: black;
  color: white;
  display: flex;
}

.Navbar{
    margin-left: 1028px;
    margin-top: 5px;
    display: flex;
}

.Navbar button{
    margin-right: 40px;
    font-size: 20px;
    padding: 10px;
    border-radius: 7px;
    border: none;
    cursor: pointer;
}

.Navbar button:hover{
    background-color: #0F3C80;
    color: white;
    transition: 350ms;
}

.logo img{
    margin-left: 20px;
    height: 50px;
    width: 70px;
}

.Profile{
    width: 100%;
    display: block;
}

.card{
    width: 20%;
    margin: auto;
    margin-top: 100px;
    text-align: center;
    border-radius: 15px;
    font-size: 20px;
    box-shadow: 0 8px 10px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}

.card img{
    width: 100%;
    height: 250px;
    background-size: cover;
}

.card h1{
    padding: 10px 0 10px 0;
    font-size: 28px;
}

.card p{
    padding-bottom: 20px;
}

.loading img{
    height: 500px;
    width: 500px;
    margin-left: 420px;
}